<!---->
<template>
  <!---->
  <el-card class="server-card">
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="editServerForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editServerForm.Name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-row>
            <el-col :span="14"
              ><el-input
                v-model.trim="editServerForm.IP"
                autocomplete="off"
                placeholder="IP"
              ></el-input>
            </el-col>
            <el-col :span="2">:</el-col>
            <el-col :span="8"
              ><el-input
                v-model.trim="editServerForm.Port"
                autocomplete="off"
                placeholder=""
              ></el-input
            ></el-col>
          </el-row>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model.trim="editServerForm.UserName"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model.trim="editServerForm.Password"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            type="textarea"
            v-model.trim="editServerForm.Desc"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="commitEdit"> </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-divider content-position="left"></el-divider>
    <el-table :data="servers" class="server-table">
      <el-table-column label="ID" align="center">
        <template slot-scope="scope">
          <i class="el-icon-arrow-right"></i>
          <span style="margin-left: 10px">{{ scope.row.ID }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.Name }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" min-width="170px">
        <template slot-scope="scope">
          <span>{{ scope.row.IP }}:{{ scope.row.Port }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>Brook: {{ scope.row.Installed }}</p>
            <p>Brook: {{ scope.row.BStatus }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag
                size="medium"
                type="success"
                v-if="scope.row.Status == ''"
                >{{ scope.row.Status }}</el-tag
              >
              <el-tag
                size="medium"
                type="info"
                v-if="scope.row.Status == ''"
                >{{ scope.row.Status }}</el-tag
              >
              <el-tag
                size="medium"
                type="danger"
                v-if="scope.row.Status == ''"
                >{{ scope.row.Status }}</el-tag
              >
              <el-tag
                size="medium"
                type="warning"
                v-if="scope.row.Status == ''"
                >{{ scope.row.Status }}</el-tag
              >
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" min-width="150px">
        <template slot-scope="scope">
          <!--  -->
          <el-popover trigger="hover" placement="top">
            <p>{{ scope.row.Desc }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag>{{ shortenDesc(scope.row.Desc) }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" min-width="120px">
        <template slot-scope="scope">
          <el-dropdown @command="handleCommand">
            <el-button :key="scope.$row" class="more-btn">
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <!--  -->
              <el-dropdown-item
                :command="beforeHandleCommand(scope.$index, scope.row, 'edit')"
                ></el-dropdown-item
              >

              <el-dropdown-item
                :command="
                  beforeHandleCommand(scope.$index, scope.row, 'delete')
                "
                ></el-dropdown-item
              >
              <el-dropdown-item
                :command="beforeHandleCommand(scope.$index, scope.row, 'start')"
                ></el-dropdown-item
              >
              <el-dropdown-item
                :command="beforeHandleCommand(scope.$index, scope.row, 'stop')"
                ></el-dropdown-item
              >
              <el-dropdown-item
                :command="
                  beforeHandleCommand(scope.$index, scope.row, 'restart')
                "
                ></el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
</template>
<script>
export default {
  name: 'ServerCard',
  props: ['servers'],
  data() {
    return {
      index: 0,
      opServer: {},
      dialogFormVisible: false,
      editServerForm: {
        BStatus: 1,
      },
      formLabelWidth: '120px',
    }
  },
  methods: {
    getRowKeys(row) {
      return row.individualId // 
    },
    beforeHandleCommand(index, row, command) {
      //

      return { index, row, command }
    },
    shortenDesc(desc) {
      if (desc.length > 8) {
        //
        return desc.substr(0, 8) + '...'
      } else {
        //
        return desc
      }
    },
    handleCommand(command) {
      //console.log(JSON.stringify(command))
      //command.row
      //command.command
      let server = command.row
      switch (command.command) {
        case 'delete':
          //
          this.$confirm(
            `${server.Name}${server.recordsNum}()`,
            '',
            {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning',
            }
          )
            .then(() => {
              //
              let formData = new FormData()
              formData.append('Username', this.$store.state.username)
              formData.append('Password', this.$store.state.password)
              formData.append('ID', server.ID)
              this.$axios
                .post(
                  this.$store.state.webServerUrl + '/web/delserver',
                  formData
                )
                .then((response) => {
                  if (response.data.Code == 200) {
                    this.$message({
                      type: 'success',
                      message: '!',
                    })
                  } else {
                    this.$message({
                      type: 'error',
                      message: ':' + JSON.stringify(response.data),
                    })
                  }
                })
                .catch((err) => {
                  this.$message({
                    type: 'error',
                    message: ':' + JSON.stringify(err),
                  })
                })
              //
              this.$common.getData(this)
            })
            .catch(() => {
              this.$message({
                type: 'info',
                message: '',
              })
            })
          break
        case 'edit':
          //
          this.dialogFormVisible = true
          this.editServerForm = server
          break
        case 'start': {
          //brook
          //
          let fd = new FormData()
          fd.append('Username', server.UserName)
          fd.append('Password', server.Password)
          this.$axios
            .post(`http://${server.IP}:${server.Port}/api/start`, fd)
            .then((r) => {
              if (r.data.Code == 200) {
                this.$message({
                  type: 'success',
                  message: 'Brook',
                })
              } else {
                this.$message({
                  type: 'error',
                  message: 'Brook' + r.data.Msg,
                })
              }
            })
            .catch((e) => {
              this.$message({
                type: 'error',
                message: 'Brook' + JSON.stringify(e),
              })
            })
          this.$common.getData(this)
          break
        }
        case 'stop': {
          //brook
          let fd = new FormData()
          fd.append('Username', server.UserName)
          fd.append('Password', server.Password)
          this.$axios
            .post(`http://${server.IP}:${server.Port}/api/stop`, fd)
            .then((r) => {
              if (r.data.Code == 200) {
                this.$message({
                  type: 'success',
                  message: 'Brook',
                })
              } else {
                this.$message({
                  type: 'error',
                  message: 'Brook' + r.data.Msg,
                })
              }
            })
            .catch((e) => {
              this.$message({
                type: 'error',
                message: 'Brook' + JSON.stringify(e),
              })
            })
          this.$common.getData(this)
          break
        }
        case 'restart': {
          //brook
          let fd = new FormData()
          fd.append('Username', server.UserName)
          fd.append('Password', server.Password)
          this.$axios
            .post(`http://${server.IP}:${server.Port}/api/restart`, fd)
            .then((r) => {
              if (r.data.Code == 200) {
                this.$message({
                  type: 'success',
                  message: 'Brook',
                })
              } else {
                this.$message({
                  type: 'error',
                  message: 'Brook' + r.data.Msg,
                })
              }
            })
            .catch((e) => {
              this.$message({
                type: 'error',
                message: 'Brook' + JSON.stringify(e),
              })
            })
          this.$common.getData(this)
          break
        }

        case 'print':
          //TODO:brook
          break
      }
    },
    commitEdit() {
      //
      //
      let compeleted = true
      //console.log('' + JSON.stringify(this.editServerForm))
      for (let i in this.editServerForm) {
        if (this.editServerForm[i] === '') {
          this.$message({
            type: 'warning',
            message: '' + i + '',
          })
          compeleted = false
        }
      }
      if (compeleted) {
        let formData = new FormData()
        formData.append('ServerUsername', this.editServerForm.UserName)
        formData.append('ServerPassword', this.editServerForm.Password)
        formData.append('Username', this.$store.state.username)
        formData.append('Password', this.$store.state.password)
        formData.append('Name', this.editServerForm.Name)
        formData.append('IP', this.editServerForm.IP)
        formData.append('Port', this.editServerForm.Port)
        formData.append('Desc', this.editServerForm.Desc)
        formData.append('ID', this.editServerForm.ID)
        this.$axios
          .post(this.$store.state.webServerUrl + '/web/editserver', formData)

          .then((r) => {
            if (r.data.Code == 200) {
              this.$message({
                type: 'success',
                message: '',
              })
            } else {
              this.$message({
                type: 'error',
                message: '' + r.data.Msg,
              })
            }
          })
          .catch((e) => {
            this.$message({
              type: 'error',
              message: '' + JSON.stringify(e),
            })
          })
        //
        this.dialogFormVisible = false
        this.$common.getData(this)
      }
    },
  },
  computed: {},
}
</script>
<style>
.more-btn {
  background: linear-gradient(
    90deg,
    rgba(31, 51, 212, 1) 0%,
    rgba(51, 69, 223, 1) 48%,
    rgba(72, 88, 241, 1) 100%
  ) !important;
  color: whitesmoke !important;
}
@media screen and (min-width: 480px) {
  .server-card {
    border-radius: 0.5rem !important;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    margin-top: 2.5%;
  }
}

@media screen and (max-width: 479px) {
  .server-card {
    padding: 1rem 0.5%;
    border-radius: 0.5rem !important;
    border: 1px solid #eaeaea;
    box-shadow: 0 2 05px #cac6c6;
    margin-top: 2.5%;
  }
}
</style>
