<!---->
<template>
  <el-card class="forward-card">
    <el-divider content-position="left"></el-divider>
    <el-table :data="forwards" style="width: 100%">
      <el-table-column label="#" align="center">
        <template slot-scope="scope">
          <i class="el-icon-d-arrow-right"></i>
          <span style="margin-left: 10px">{{ scope.$index + 1 }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" min-width="150px">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>: {{ scope.row.rname }}</p>
            <p>ip: {{ scope.row.lhost }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.rname }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
        label="/"
        align="center"
        min-width="170px"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.lhost }}:{{ scope.row.lport }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="/"
        align="center"
        min-width="170px"
      >
        <template slot-scope="scope">
          <span>{{ scope.row.rhost }}:{{ scope.row.rport }}</span>
        </template></el-table-column
      >
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-tag type="success" v-if="scope.row.enable"></el-tag>
          <el-tag type="danger" v-if="!scope.row.enable"></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="(/)" align="center" min-width="150px">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>
              tcp(/) {{ scope.row.tcpOut }}/{{ scope.row.tcpIn }}
            </p>
            <p>
              udp(/): {{ scope.row.udpOut }}/{{ scope.row.udpIn }}
            </p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium"
                >{{ scope.row.totalOut }}/{{ scope.row.totalIn }}</el-tag
              >
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" min-width="150px">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>{{ scope.row.note }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag>{{ shortenDesc(scope.row.note) }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" min-width="150px">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="stopForward(scope.$index, scope.row)"
            v-if="scope.row.enable"
          >
            
          </el-button>
          <el-button
            size="mini"
            @click="startForward(scope.$index, scope.row)"
            v-if="!scope.row.enable"
          >
            
          </el-button>
          <el-popconfirm
            title=""
            style="margin-left: 0.2rem"
            @onConfirm="deleteForward(scope.$index, scope.row)"
          >
            <el-button slot="reference" type="danger" size="mini"
              ></el-button
            >
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
</template>
<script>
export default {
  name: 'ForwardCard',
  props: ['forwards'],
  data() {
    return {
      index: 1,
    }
  },
  methods: {
    shortenDesc(desc) {
      if (desc.length > 8) {
        //
        return desc.substr(0, 8) + '...'
      } else {
        //
        return desc
      }
    },
    stopForward: function(index, row) {
      //
      //console.log('' + index + JSON.stringify(row))
      let formData = new FormData()
      formData.append('Username', row.username)
      formData.append('Password', row.password)
      formData.append('LocalPort', row.lport)
      formData.append('Enable', 0)
      this.$axios
        .post(`http://${row.lhost}:${row.controlport}/api/switchpf`, formData)
        .then((r) => {
          //console.log('\n' + JSON.stringify(r.data))
          if (r.data.Code == 200) {
            this.$notify({
              title: '',
              message: '',
              type: 'success',
            })
          } else {
            this.$notify({
              title: '',
              message: '' + r.data.Msg,
              type: 'error',
            })
          }
        })
        .catch((e) => {
          //console.log('\n' + JSON.stringify(e))
          this.$notify({
            title: '',
            message: '' + JSON.stringify(e),
            type: 'error',
          })
        })
      this.$common.getData(this)
    },
    startForward: function(index, row) {
      //console.log('' + index + JSON.stringify(row))
      let formData = new FormData()
      formData.append('Username', row.username)
      formData.append('Password', row.password)
      formData.append('LocalPort', row.lport)
      formData.append('Enable', 1)
      this.$axios
        .post(`http://${row.lhost}:${row.controlport}/api/switchpf`, formData)
        .then((r) => {
          //console.log('\n' + JSON.stringify(r.data))
          if (r.data.Code == 200) {
            this.$notify({
              title: '',
              message: '',
              type: 'success',
            })
          } else {
            this.$notify({
              title: '',
              message: '' + r.data.Msg,
              type: 'error',
            })
          }
        })
        .catch((e) => {
          //console.log('\n' + JSON.stringify(e))
          this.$notify({
            title: '',
            message: '' + JSON.stringify(e),
            type: 'error',
          })
        })
      this.$common.getData(this)
    },
    deleteForward: function(index, row) {
      //
      //console.log('' + index + JSON.stringify(row))
      let formData = new FormData()
      formData.append('Username', row.username)
      formData.append('Password', row.password)
      formData.append('LocalPort', row.lport)
      this.$axios
        .post(`http://${row.lhost}:${row.controlport}/api/delpf`, formData)
        .then((r) => {
          //console.log('\n' + JSON.stringify(r.data))
          if (r.data.Code == 200) {
            this.$notify({
              title: '',
              message: '',
              type: 'success',
            })
          } else {
            this.$notify({
              title: '',
              message: '' + r.data.Msg,
              type: 'error',
            })
          }
        })
        .catch((e) => {
          //console.log('\n' + JSON.stringify(e))
          this.$notify({
            title: '',
            message: '' + JSON.stringify(e),
            type: 'error',
          })
        })
      //
      this.$common.getData(this)
    },
  },
}
</script>
<style>
.forward-card {
  border-radius: 0.5rem !important;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  margin-top: 2.5%;
}
</style>
